export default {
  data() {
    return {
      isSelect: false,
      addressList: []
    };
  },
  onShow() {

    uni.getStorage({
      key: 'delAddress',
      success: (e) => {
        let len = this.addressList.length;
        if (e.data.hasOwnProperty('id')) {
          for (let i = 0; i < len; i++) {
            if (this.addressList[i].id == e.data.id) {
              this.addressList.splice(i, 1);
              break;
            }
          }
        }
        uni.removeStorage({
          key: 'delAddress'
        })
      }
    })
    uni.getStorage({
      key: 'saveAddress',
      success: (e) => {
        let len = this.addressList.length;
        if (e.data.hasOwnProperty('id')) {
          for (let i = 0; i < len; i++) {
            if (this.addressList[i].id == e.data.id) {
              this.addressList.splice(i, 1, e.data);
              break;
            }
          }
        } else {
          let lastid = this.addressList[len - 1];
          lastid++;
          e.data.id = lastid;
          this.addressList.push(e.data);
        }
        uni.removeStorage({
          key: 'saveAddress'
        })
      }
    })
  },
  onLoad(e) {
    if (e.type == 'select') {
      this.isSelect = true;
    }
    this.fetchArsList();
  },
  methods: {
    //获取地址列表信息
    fetchArsList(){
      let url = "address/addressList";
      this.$Request.get(url).then( res => {
        if(res && res.code == 200){
          this.addressList = res.data;
          console.log(this.addressList);
        }
      })
    },
    edit(row) {
      uni.setStorage({
        key: 'address',
        data: row,
        success() {
          uni.navigateTo({
            url: "/pages/address/edit/edit?type=edit"
          })
        }
      });

    },
    add(){
      uni.navigateTo({
        url: "edit/edit?type=add"
      })
    },
    select(row) {
      //是否需要返回地址(从订单确认页跳过来选收货地址)
      if (!this.isSelect) {
        return;
      }
      uni.setStorage({
        key: 'selectAddress',
        data: row,
        success() {
          uni.navigateBack();
        }
      })
    }
  }
}
